$leftColor: #7ACEE6;
$centerColor: #FB4C98;
$rightColor: #7972F3;
$itemSize: 20px;
$animationDuration: 2s;

.tcLoader {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
  backdrop-filter: blur(1px);

  .loaderAnimationWrap {
    display: flex;
    animation: animationBase $animationDuration ease both infinite;

    .loaderItem {
      width: calc(#{$itemSize});
      height: calc(#{$itemSize});
      border-radius: 50%;
      margin: 0 calc(#{$itemSize} / 2);

      &.loaderItemLeft {
        animation: animationItem $animationDuration ease both infinite;
        background: $leftColor;
      }

      &.loaderItemCenter {
        animation: animationItem $animationDuration ease both infinite;
        animation-delay: .3s;
        background: $centerColor;
      }

      &.loaderItemRight {
        animation: animationItem $animationDuration ease both infinite;
        animation-delay: .4s;
        background: $rightColor;
      }
    }
  }
}


@keyframes animationBase {
  0% {
    transform: rotate(0) scale(.3);
  }

  30% {
    transform: rotate(0) scale(1);
  }

  70% {
    transform: rotate(0) scale(.9);
  }

  100% {
    transform: rotate(360deg) scale(.3);
  }
}

@keyframes animationItem {
  0% {
    transform: scale(1);
  }

  30% {
    transform: scale(1);
  }

  70% {
    transform: scale(1.4);
  }

  100% {
    transform: scale(1);
  }
}
