
.FAE-editable {
  font-family: TiemposHeadline;
  color: #202020;
  font-size: 18px;
  line-height: 31px;
  display: inline-block;
  padding-left: 5px;

  &.lowConfidence {
    color: #7126c1;
    font-style: italic;
  }

  &-divider {
    display: block;
    height: 48px;
  }

  &.FAE-editable-punctuation {
    padding-left: 0;
    padding-right: 5px;
  }

  &.FAE-editable-spacer {
    margin: 0;
    padding: 0;
    width: 0;
  }
}