/* SASS Variables Including STARTS */
@import 'variables';

/* SASS Variables Including ENDS */


/* Page Fonts Including STARTS */

@charset "utf-8";


@font-face {
  font-family: 'AvertaStd';
  src: url("../fonts/AvertaStd-Regular.woff2") format('woff2'), url("../fonts/AvertaStd-Regular.woff") format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'AvertaStd';
  src: url("../fonts/AvertaStd-Semibold.woff2") format('woff2'), url("../fonts/AvertaStd-Semibold.woff") format('woff');
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: 'AvertaStd';
  src: url("../fonts/AvertaStd-Bold.woff2") format('woff2'), url("../fonts/AvertaStd-Bold.woff") format('woff');
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'AvertaStd';
  src: url("../fonts/AvertaStd-Extrabold.woff2") format('woff2'), url("../fonts/AvertaStd-Extrabold.woff") format('woff');
  font-weight: 800;
  font-style: normal;
}

@font-face {
  font-family: 'TiemposHeadline';
  src: url("../fonts/TiemposHeadline-Regular.woff2") format('woff2'), url("../fonts/TiemposHeadline-Regular.woff") format('woff');
  font-weight: normal;
  font-style: normal;
}


/* Page Fonts Including ENDS */

/* Common CSS STARTS */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  position: relative !important;
  height: auto !important;
}

#TCArticle-editor {
  font-family: AvertaStd;
  font-size: 18px;
}

