.FAE-main-wrapper {
  header {
    width: 100%;
    height: 60px;
    position: fixed;
    top: 0;
    left: 0;
    box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.1);
    background-color: #ffffff;
    padding: 0 16px;
    z-index: 1;

    .FAE-header-button {
      border-radius: 4px;
      padding: 7px 20px;
      min-width: 135px;
      margin-left: 16px;
      border: solid 1px #808080;
      line-height: 1.43;
      color: #808080;
      font-size: 14px;
      cursor: pointer;
      display: inline-block;

      &.active {
        background-color: #4d4d4d;
        color: #ffffff;

        &:hover {
          color: #ffffff;
        }
      }

      &:hover {
        color: #8a8a8a;
        border: 1px solid #5c5a5a;
      }
    }

    .FAE-header-logo {
      height: 50%;
      position: absolute;
      top: 15px;
    }

    .FAE-header-right {
      position: absolute;
      right: 16px;
      top: 11px;
    }
  }
}
