.FAE-main-wrapper {
  margin-top: 65px;
  position: relative;
  display: inline-block;
  background: white;
  width: 100%;
  height: auto;
  overflow-x: hidden;

  &.loading {
    pointer-events: none;
  }

  .FAE-content-wrapper {
    max-width: 1320px;
    margin: auto;
    padding: 0 60px 45px 60px;
  }

  .FAE-left-wrapper {
    max-width: 620px;
    width: 50%;
  }

  .FAE-title {
    font-size: 56px;
    font-weight: 800;
    line-height: 1.29;
    letter-spacing: -0.4px;
    color: #202020;
    margin-top: 25px;
    margin-bottom: 48px;
    padding-left: 5px;
    word-break: break-word;
  }

  .FAE-placeholder {
    text-align: center;
    font-style: italic;
    font-size: 24px;
    letter-spacing: 1.2px;
    color: #202020;
    font-weight: 600;
  }
}

@media only screen and (max-width: 823px) {
  .FAE-main-wrapper {
    .FAE-content-wrapper {
      padding-left: 20px;
      padding-right: 20px;
    }

    .FAE-left-wrapper {
      width: 100%;
      max-width: 100%;
    }
  }
}